<template>
    <div>
        <svg ref='modelo'


            xmlns:dc="http://purl.org/dc/elements/1.1/"
            xmlns:cc="http://creativecommons.org/ns#"
            xmlns:rdf="http://www.w3.org/1999/02/22-rdf-syntax-ns#"
            xmlns:svg="http://www.w3.org/2000/svg"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 21000 29700"
            height="700"
            width="700"
            xml:space="preserve">
            <gola :cor="corGola"></gola>
            <camisa :cor="corCamisa"></camisa>
            <manga :cor="corManga"></manga>
            <punho :cor="corPunho"></punho>
            <numero :cor="corNumero"></numero>
            <calcao :cor="corCalcao"></calcao>
            <meiao :cor="corMeiao"></meiao>
        </svg>



        <div class="colors">        
            <div class="color-item">
                <h6> Camisa: </h6>
                <compact value="colors" :palette="palette" @input="onChangeCorCamisa" />
            </div>
            <div class="color-item">
                <h6> Calção: </h6> 
                <compact value="colors" :palette="palette" @input="onChangeCorCalcao" />
            </div>
            <div class="color-item">
                <h6> Manga:  </h6>
                <compact value="corManga" :palette="palette" @input="onChangeCorManga" /> 
            </div>
            
        </div> 
        <div class="colors">
            <div class="color-item">
                <h6> Punho:  </h6>
                <compact value="colors" :palette="palette" @input="onChangeCorPunho" /> 
            </div>
            <div class="color-item">
                <h6> Meião:  </h6>
                <compact value="colors" :palette="palette" @input="onChangeCorMeiao" /> 
            </div>
            <div class="color-item">
                <h6> Número: </h6>
                <compact value="colors" :palette="palette" @input="onChangeCorNumero" /> 
            </div>
            <div class="color-item">
                <h6> Gola:   </h6>
                <compact value="corGola" :palette="palette" @input="onChangeCorGola" /> 
            </div>
            <div class="salvar-imagem">
                <button v-on:click="savePng">Salvar Imagem</button>
            </div>
        </div> 
        
        

    </div>
</template>
<script>
import Calcao from '@/components/base/Calcao'
import Camisa from '@/components/base/Camisa'
import Gola   from '@/components/base/Gola'
import Manga  from '@/components/base/Manga'
import Meiao  from '@/components/base/Meiao'
import Numero from '@/components/base/Numero'
import Punho  from '@/components/base/Punho'
import SvgSaver from 'svgsaver'


const palette = [
  '#4D4D4D', '#999999', '#FFFFFF', '#F44E3B', '#FE9200', '#FCDC00',
  '#DBDF00', '#A4DD00', '#68CCCA', '#73D8FF', '#AEA1FF', '#FDA1FF',
  '#333333', '#808080', '#CCCCCC', '#D33115', '#E27300', '#FCC400',
  '#B0BC00', '#68BC00', '#16A5A5', '#009CE0', '#7B64FF', '#FA28FF',
  '#000000', '#666666', '#B3B3B3', '#9F0500', '#C45100', '#FB9E00',
  '#808900', '#194D33', '#0C797D', '#0062B1', '#653294', '#AB149E'
]


import {Compact} from 'vue-color'
export default {
    name: 'Simulador',
    data(){
        return {
            corCamisa: '#68CCCA',
            corCalcao: '#0f0',
            corGola: '#68CCCA',
            corManga: '#68CCCA',
            corMeiao: '#ff0',
            corPunho: 'f0f',
            corNumero: '#fff',
            palette: palette,
            colors: {
                hex: '#194d33',
                hsl: {
                    h: 150,
                    s: 0.5,
                    l: 0.2,
                    a: 1
                },
                hsv: {
                    h: 150,
                    s: 0.66,
                    v: 0.30,
                    a: 1
                },
                rgba: {
                    r: 25,
                    g: 77,
                    b: 51,
                    a: 1
                },
                    a: 1
            }

        }
    },
    components: {
        Compact,
        Calcao,
        Camisa,
        Gola,
        Manga,
        Meiao,
        Numero,
        Punho
    },
    methods: {
        savePng(){
           let svgsaver = new SvgSaver()
           svgsaver.asPng(this.$refs.modelo, 'modelo.png')
        },
        onChangeCorCamisa(val){
            this.corCamisa = val.hex
        },
        onChangeCorCalcao(val){
            this.corCalcao = val.hex
        },
        onChangeCorMeiao(val){
            this.corMeiao = val.hex
        },
        onChangeCorNumero(val){
            this.corNumero = val.hex
        },
        onChangeCorPunho(val){
            this.corPunho = val.hex
        },
        onChangeCorManga(val){
            this.corManga = val.hex
        },
        onChangeCorGola(val){
            this.corGola = val.hex
        }
    }
}
</script>

<style scoped>
    .color-item{
        text-align: left;
    }
    .colors{
        float: left;
        margin: 10px;
    }
    .salvar-imagem{
        padding: 10px;
    }
    .salvar-imagem > button{
        padding: 20px;
        font-size: 1em;
        font-weight: bold;
        border: none;
    }
</style>



